}
.about__list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}
@media (max-width: 576px) {
  .about__list {
    grid-template-columns: repeat(4, 1fr);
  }
}
.about__item {
  grid-column: 3 span;
}
@media (max-width: 1200px) {
  .about__item {
    grid-column: 6 span;
  }
}
.about__digit {
