  .benefits__list {
    grid-template-columns: repeat(4, 1fr);
  }
}
.benefits__item {
  grid-column: 4 span;
}
@media (max-width: 576px) {
  .benefits__item {
    grid-column: 4 span;
  }
}
@media (max-width: 1200px) {
  .benefits__item {
    grid-column: 6 span;
  }
}
.benefits__subtitle {
  display: inline-flex;
  align-items: center;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 24px;
  line-height: 160%;
