}
.trainers__image {
  border-radius: 20px;
  margin-bottom: 20px;
  max-height: 500px;
}
.trainers__name {
  padding-top: 20px;
  font-weight: 600;
  font-size: 24px;
  line-height: 160%;
  color: var(--dark-color);
}
.trainers__post {
  padding: 10px 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
